import { motion } from 'framer-motion'
import React from 'react'
import styled from 'styled-components'
import about from '../data/about'

const About = () => {
    const about_variant = {
        hidden:{
            opacity:0
        },
        visible:{
            opacity: 1,
            transition:{
                delay:0.2, duration:0.5,
            }
        }
    }
    return (
        <Wrapper>
            <motion.div 
            variants={about_variant}
            initial='hidden'
            animate='visible'
            className='about'>
                <p>Hi, this is Jahid Hasan and I’m passionate about all thing about JavaScript. I work hard and care about writing clean, efficient and reusable codes</p>
                <div className="services">
                    <h4 className='fw-bold text-uppercase py-4'>what i offer</h4>
                    <div className="row">
                        {
                            about.map((data, index) => (
                                <div className="col-lg-6" key={index}>
                                    <div className="card my-2 shadow">
                                        <div className="card-body text-center">
                                            {data.icon} <p className="lead">{data.title}</p>
                                            <p>{data.desc}</p>
                                            <strong>{data.tools}</strong>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }


                    </div>

                </div>
            </motion.div>
        </Wrapper>
    )
}
const Wrapper = styled.section`
    .about{
        max-height: 80vh;
    }
`


export default About
